import { Component, Input, OnInit} from '@angular/core';


export class SimpleChartComponent implements OnInit {
   percent: any;
   barColor: string;
  public options: any = {
    barColor: null,
    trackColor: '#f9f9f9',
    scaleColor: '#dfe0e0',
    scaleLength: 5,
    lineCap: 'round',
    lineWidth: 3,
    size: 110,
    rotate: 0,
    animate: {
        duration: 1000,
        enabled: true
    }
  };

  
  constructor() {
  }

  ngOnInit(){
    this.options.barColor = "#"+this.barColor;
  }
  
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'simple-chart',
  template: `
    <span class="min-chart">
      <span class="percent">{{ percent }}</span>
      <easy-pie-chart [percent]="percent" [options]="options"></easy-pie-chart>
    </span>
  `,
  styles: []
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'percent': [{ type: Input, args: ['percent', ] },],
'barColor': [{ type: Input, args: ['barColor', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
